import React from 'react'
import classnames from 'classnames'
import $ from 'jquery'
import moment from 'moment'

import heartIcon from 'image/love.png'
import moon from 'image/moon.png'
import fireworks from 'image/fireworks.png'
import myAvatar from 'image/i.png'
import herAvatar from 'image/she.png'
import XIU from 'audio/xiu.mp3'
import BGM from 'audio/envelopBgm.mp3' // 
import METEORS from 'publicJs/meteor'
import FIREWORKS from 'publicJs/fireworks'
import style from './envelop.less'
import fireStyle from '../fireworks/fireworks.scss'
/* eslint-disable */

const TIME = '2019-03-06 19:35:00'
const timePad = str => `${str}`.padStart(2, '0')

class Envelop extends React.Component {
    state = {
        envAnimationType: 'leave',
        conAnimationType: 'enter',
        showEnvelop: false,
        showConfess: false,
        showFire: false,
        firstPage: true,
        show: true,
    }
    interval = -1
    componentDidMount = _ => {
        METEORS.START()
        const pow = new Audio(XIU)
        pow.play()
        setTimeout(() => {
            this.setState({
                envAnimationType: 'enter',
                showEnvelop: true,
            })
            this.getTime()
        }, 500)
        // setTimeout(() => this.bgmAudio.play(), 2000)
    }

    onClickHeart = _ => {
        this.setState({
            envAnimationType: 'leave',
        }, () => {
            setTimeout(() => {
                this.setState({
                    showConfess: true,
                }, () => {
                    this.bgmAudio.play()
                    this.print()
                })
            }, 600)
        })
    }

    closeModal = _ => {
        this.setState({
            showEnvelop: false,
        })
    }

    envAnimationEnd = _ => {
        if (this.state.envAnimationType === 'leave') {
            this.closeModal()
        }
    }

    conAnimationEnd = _ => {
        const { history } = this.props
        if (this.state.conAnimationType === 'leave') {
            // history.replace('/fireworks')
            // 
            this.setState({
                firstPage: false,
            }, () => {
                FIREWORKS.START(() => {
                    this.setState({
                        show: false,
                    })
                })
            })
        }
    }

    getTime = _ => {
        this.interval = setInterval(() => {
            const diff = moment().diff(moment(TIME))
            const days = moment().diff(moment(TIME), 'days')
            const hours = moment.duration(diff).hours()
            const minutes = moment.duration(diff).minutes()
            const seconds = moment.duration(diff).seconds()
            this.setState({
                days: timePad(days),
                hours: timePad(hours),
                minutes: timePad(minutes),
                seconds: timePad(seconds),
            })
        }, 1000)
    }

    startFire = _ => {
        // 
        this.setState({
            conAnimationType: 'leave',
        })
    }

    print = () => {
        const that = this
        $.fn.autotype = function () {
            const _this = $(this);
            let str = _this.html();
            // 
            str = str.replace(/(\s){2,}/g, "$1");
            let index = 0;
            $(this).html('');
            const timer = function fn() {
                const args = arguments;
                const current = str.slice(index, index + 1);
                // html,<p>
                if (current == '<') {
                    index = str.indexOf('>', index) + 1;
                }
                else {
                    index++;
                }
                //: setInterval_
                if (index < str.length - 1) { //2
                    _this.html(str.substring(0, index) + (index & 1 ? '_' : ''));
                    setTimeout(fn, 200)
                } else {
                    _this.html(str.substring(0, index));
                    clearTimeout(timer);
                    // 
                    setTimeout(() => {
                        that.setState({
                            showFire: true,
                        })
                    }, 200)
                };
            };
            // 1s
            setTimeout(timer, 1000);
        };
        $("#autotype").autotype();
    }

    render() {
        const {
            firstPage, envAnimationType, conAnimationType, showEnvelop, showConfess, showFire, show, days, hours, minutes, seconds,
        } = this.state
        if (firstPage) {
            return (
                <div className={style.box}>
                    <canvas id="meteor" className={style.canvas} />
                    <img src={moon} className={style.moon} alt="" />
                    {showEnvelop &&
                    <div
                        onAnimationEnd={this.envAnimationEnd}
                        className={classnames(style.envelop, style[`envelop-${envAnimationType}`])}
                    >
                        <div className={style['triangle-down']} />
                        <img className={style.heart} src={heartIcon} onClick={this.onClickHeart} alt="" />
                        <div className={style['triangle-up']} />
                        <div className={style.text}></div>
                        <img src={myAvatar} alt="" className={style['my-avatar']} />
                        <img src={herAvatar} alt="" className={style['her-avatar']} />
                    </div>}
                    {showConfess &&
                    <div className={style['confess-date']}>
                        :
                        <span className={style['confess-time']}>{days}</span>
                        <span className={style['confess-time']}>{hours}</span>
                        <span className={style['confess-time']}>{minutes}</span>
                        <span className={style['confess-time']}>{seconds}</span>
                    </div>}
                    {showConfess &&
                    <div
                        id="autotype"
                        onAnimationEnd={this.conAnimationEnd}
                        className={classnames(style['confess-main'], style[`confess-${conAnimationType}`])}
                    >
                        <h2 className={style['confess-title']}></h2>
                        <p></p>
                        <p>
                            </p>
                        <p></p>
                        <p className={style.suffix} style={{ paddingTop: 10 }}>Love u forever and everby leo</p>
                        <p className={style['suffix-date']}>20190925</p>
                    </div>}
                    {showFire && <img src={fireworks} onClick={this.startFire} className={style['confess-fire']} />}
                    <audio
                        autoPlay={false}
                        preload="metadata"
                        ref={v => { this.bgmAudio = v }}
                        src={BGM}
                        controls={true}
                        style={{ display: 'none' }}
                        loop={true}
                    />
                </div>
            )
        }
        return (
            <div className={fireStyle.box}>
                {show && <div data-text="" className={fireStyle.text}></div>}
                <canvas id="canvas" className={fireStyle['canvas-1']} />
                <canvas className={classnames('canvas', [fireStyle['canvas-2']])} />
                <div className="overlay" style={{ display: 'none' }}>
                    <div className="tabs">
                        <div className="tabs-labels">
                            <span className="tabs-label">leo</span>
                            <span className="tabs-label">love</span>
                            <span className="tabs-label">cc</span>
                        </div>
                        <div className="tabs-panels">
                            <ul className="tabs-panel commands" />
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Envelop
