// main
import Main from 'containers/Main'

import Envelop from 'component/envelop/Index'
import Fireworks from 'component/fireworks/Index'
import NotFound from 'component/NotFound'

const routes = [{
    path: '/',
    component: Main,
    routes: [
        {
            exact: true,
            path: '/',
            component: Envelop,
        },
        {
            exact: true,
            path: '/fireworks',
            component: Fireworks,
        },
        {
            component: NotFound,
        },
    ],
}]

export default routes
