// import 'antd-mobile/dist/antd-mobile.less'
import 'babel-polyfill'
import React from 'react'
import ReactDOM from 'react-dom'
import { Provider } from 'react-redux'
import { createStore, applyMiddleware, compose, combineReducers } from 'redux'
import { createBrowserHistory } from 'history'

import { connectRouter, routerMiddleware, ConnectedRouter } from 'connected-react-router'
import createSagaMiddleware from 'redux-saga'

import './styles/less/index.less'
import rootReducer from './reducers'
import AppRouter from './router'
import sagas from './sagas'

import './flexible.js'

const composeEnhancers = process.env.NODE_ENV !== 'production' ? (global.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose) : compose

//  saga middleware
const sagaMiddleware = [createSagaMiddleware()]
const enhancer = composeEnhancers(applyMiddleware(...sagaMiddleware))
const history = createBrowserHistory()
const initialState = {}

const store = createStore(
    connectRouter(history)(combineReducers(rootReducer)), // new root reducer with router state
    initialState,
    enhancer,
    // composeEnhancers(
    //     applyMiddleware(
    //         routerMiddleware(history), // for dispatching history actions
    //         // ... other middlewares ...
    //     ),
    // ),
)

sagaMiddleware[0].run(sagas)

ReactDOM.render(
    <Provider store={store}>
        <ConnectedRouter history={history}>
            <AppRouter />
        </ConnectedRouter>
    </Provider>,
    document.getElementById('app'),
)
