import React from 'react'
import classnames from 'classnames'
import FIREWORKS from 'publicJs/fireworks'
import style from './fireworks.scss'

class Fireworks extends React.Component {
    state = {
        show: true,
    }
    componentDidMount = _ => {
        FIREWORKS.START(() => {
            this.setState({
                show: false,
            })
        })
    }

    render() {
        const { show } = this.state
        return (
            <div className={style.box}>
                {show && <div data-text="" className={style.text}></div>}
                <canvas id="canvas" className={style['canvas-1']} />
                <canvas className={classnames('canvas', [style['canvas-2']])} />
                <div className="overlay" style={{ display: 'none' }}>
                    <div className="tabs">
                        <div className="tabs-labels">
                            <span className="tabs-label">leo</span>
                            <span className="tabs-label">love</span>
                            <span className="tabs-label">cc</span>
                        </div>
                        <div className="tabs-panels">
                            <ul className="tabs-panel commands" />
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Fireworks
