var context;
var arr = new Array();
var starCount = 800;
var rains = new Array();
var rainCount = 20;
var windowWidth;
//context
function init() {
    //canvas
    var stars = document.getElementById("meteor");
    windowWidth = window.innerWidth; //
    stars.width = windowWidth;
    stars.height = window.innerHeight;
    //context
    context = stars.getContext("2d");
}
//
var Star = function () {
    this.x = windowWidth * Math.random(); //
    this.y = 5000 * Math.random(); //
    this.text = "."; //
    this.color = "white"; //
    //
    this.getColor = function () {
        var _r = Math.random();
        if (_r < 0.5) {
            this.color = "#333";
        } else {
            this.color = "white";
        }
    }
    //
    this.init = function () {
        this.getColor();
    }
    //
    this.draw = function () {
        context.fillStyle = this.color;
        context.fillText(this.text, this.x, this.y);
    }
}
